*{
  margin: 0px;    
      box-sizing: border-box;
}   
.container{
  width: 42.4vw;
height: 17.6vw;
margin: 0 0 2.3vw 0;
padding: 2.1vw 3.02vw 2.1vw 2.08vw;
border-radius: 17.5px;
border-style: solid;
border-width: 0;
/* border: 2px solid ; */
display: flex;
  flex-direction: row;
 
background-color: rgb(53, 52, 52); 
backdrop-filter: blur(8px);
background-color: #2b2b3e;
}
